import React from 'react'
import Task from './Task'


const Tasks = ({tasks,onDelete,onTog}) => {
    
  return (
    <>
        
            {tasks.map((task) =>(
            <Task key = {task.id} task = {task} onDelete={onDelete} onTog={onTog}/>
            ))}
        
    </>
  )
}

export default Tasks