import React from 'react'
import{FaTimes} from 'react-icons/fa'

const Task = ({task,onDelete,onTog}) => {
  return (
    <div className={`task ${task.reminder ?
    'reminder':'' } `} onDoubleClick={()=>onTog(task.id)}>
        <h3> {task.name} <FaTimes style={{color:'Blue', cursor:'pointer'}} onClick={()=>onDelete(task.id)}/> </h3>
        <p > {task.sex} </p>
        </div>
  )
}

export default Task