import React from 'react'
import Button from './Button'


const Header = ({title,show,text,showAdd}) => {
   
    
  return (
    <header className='header'>

        <h1> {title}</h1>
      
        <Button text={showAdd ? 'Close':'Add'} color={showAdd ? 'red':'green'} show={show}/>
     
    </header>
  )
}

export default Header